<script>
import { mapMutations } from "vuex";
export default {
    data() {
        return {
            dialogVisible: false,
            count:1
        }
    },
    created(){
        this.changePage('Shop');
    },
    methods: {
        ...mapMutations(["changePage","changeCartStatus", "saveCartList", "deleteCartShop"]),
        scrollDown() {
            window.scrollBy({
                top: window.innerHeight,
                left: 0,
                behavior: "smooth",
            });
        },
        addCart(shop) {
            this.changeCartStatus(true);
            let {id,name,price} = shop;
            this.saveCartList({id,name,price,count:1});
        },
        openVisible(){
            this.dialogVisible = true;
        },
        shutVisible(){
            this.dialogVisible = false;
        }
    },
}
</script>

